import {
    Component,
    h,
    State,
    Element,
    Listen,
    EventEmitter,
    Event,
} from '@stencil/core';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Data Tables
 * @description A menu component for exporting table data in CSV or PDF formats. Provides a user-friendly interface for exporting data from tables.
 */

@Component({
    tag: 'atui-table-export-menu',
    shadow: false,
})
export class AtuiTableExportMenu {
    @Element() el: HTMLAtuiTableExportMenuElement;
    @State() translations: any;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    /**
     * Emits id of the clicked button, either 'CSV' or 'PDF'.
     */
    @Event() atuiChange: EventEmitter;

    @Listen('atuiClick')
    clickHandler(event: Event) {
        this.atuiChange.emit((event.target as HTMLAtuiButtonElement).id);
    }

    render() {
        return (
            <atui-menu
                width={'auto'}
                align="end"
            >
                <atui-tooltip
                    slot="menu-trigger"
                    position="top"
                >
                    <atui-button
                        slot="tooltip-trigger"
                        icon="save_alt"
                        type="secondaryText"
                    />
                    <span slot="tooltip-content">
                        {this.translations.ATUI.TABLE.EXPORT_TO_FILE}
                    </span>
                </atui-tooltip>
                <div slot="menu-content">
                    <atui-button
                        type="secondaryText"
                        label={this.translations.ATUI.TABLE.EXPORT_AS_CSV}
                        id="CSV"
                    />
                    <atui-button
                        type="secondaryText"
                        label={this.translations.ATUI.TABLE.EXPORT_AS_PDF}
                        id="PDF"
                    />
                </div>
            </atui-menu>
        );
    }
}
